import React, { Component } from 'react';
import ImageGallery from 'react-image-gallery';
import { images, generateQuote } from './helpers.js';
import logo from './images/relativity-formulae.svg';
import '../node_modules/react-image-gallery/styles/css/image-gallery.css';
import './App.css';
import FormControl from 'react-bootstrap/FormControl';
import InputGroup from 'react-bootstrap/InputGroup';
import Button from 'react-bootstrap/Button';
import LoadingOverlay from 'react-loading-overlay';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDownload } from '@fortawesome/free-solid-svg-icons';
import Modal from 'react-bootstrap/Modal';
const robodog = require('@uppy/robodog');

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      font: 'Arial-Bold-Italic',
      fontSize: 16,
      message: ' - Albert Einstein',
      imageIndex: 0,
      isHidden: true,
      modalIsOpen: false,
      returnedImage: '',
      loading: false
    };
    this.generateImage = this.generateImage.bind(this);
    this.handleSelect = this.handleSelect.bind(this);
    this.handleImage = this.handleImage.bind(this);
    this.handleModal = this.handleModal.bind(this);
    this.handleHidden = this.handleHidden.bind(this);
  }

  
  async generateImage(e){
    this.setState({loading: true});
    this.setState({message: generateQuote() + this.state.message});
    return fetch(images[this.state.imageIndex].original)
    .then((response)=> response.blob())
    .then((blob) => {
      const resultPromise = robodog.upload([blob],{
        params: {
          auth: { key: 'ee723b70d56f11e8b7809f4882ea3f24' },
        "steps": {
          ":original": {
            "robot": "/upload/handle"
          },
          "resized": {
            "use": [
              ":original"
            ],
            "robot": "/image/resize",
            "result": false,
            "height": 600,
            "imagemagick_stack": "v1.0.0",
            "resize_strategy": "fit",
            "width": 600,
            "zoom": false
          },
          "watermarked": {
            "use": [
              "resized"
            ],
            "robot": "/image/resize",
            "result": true,
            "imagemagick_stack": "v1.0.0",
            "text": [
              {
                "text": this.state.message,
                "size": parseInt(this.state.fontSize),
                "font": this.state.font,
                "color": "#eeeeee",
                "valign": "bottom",
                "align": "left"
              }
            ]
          }
        }
      },
      waitForEncoding: true
      })
      resultPromise.then((bundle) =>{
        this.setState({
          modalIsOpen: true,
          loading: false,
          message: ' - Albert Einstein',
          returnedImage: bundle.results[0].ssl_url
        })
      })    
      }
    )
  }
  
  handleHidden(){
    this.setState({
      isHidden: !this.state.isHidden
    });
  }

  handleSelect(e){
    e.preventDefault();
    let key = e.target.name;
    let value = e.target.value;
    this.setState({[key]: value});
  }

  handleImage(e){
    this.setState({imageIndex: e});
  }

  handleModal(){
    this.setState({modalIsOpen:false});
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <div className="header-title">
            "Possibly the best webpage ever made" - Albert Einstein
          </div>
        </header>
        <div className="App-page">
          Generate your own Einstein quote! (Quotes possibly not said by Einstein)
        </div>
        <div className="App-gallery">
        <Modal id="modal-size" centered={true} show={this.state.modalIsOpen} onHide={this.handleModal}>
          <Modal.Body>
            <img 
              src={this.state.returnedImage}
              alt="new"
              />
            <a href={this.state.returnedImage} download >
              <FontAwesomeIcon icon={faDownload} />
            </a>
          </Modal.Body>
        </Modal>
        <ImageGallery onSlide={(e)=>this.handleImage(e)} items={images} showThumbnails={false} showFullscreenButton={false} showPlayButton={false}/>
        </div>
        <div className="App-form">
         <Button disabled={this.state.loading} onClick={(e)=> this.generateImage(e)} variant="outline-primary" >Generate a random quote!</Button>
         <div class="divider"/>
         <Button disabled={this.state.loading} onClick={()=> this.handleHidden()} variant="outline-primary" >Make your own quote!</Button>
         {!this.state.isHidden &&
         <div className="App-userinput">
         <InputGroup visibility="hidden" className="mb-3">
            <FormControl
              placeholder="Your message"
              aria-label="Your message"
              aria-describedby="basic-addon1"
              disabled={this.state.loading}
            />
            <InputGroup.Append>
              <Button disabled={this.state.loading} onClick={(e)=>this.generateImage(e)} variant="outline-primary">Submit</Button>
            </InputGroup.Append>
         </InputGroup>
        </div>
        }
        </div>
        <footer className="App-footer">
          Icons made by <a href="https://www.freepik.com/" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/" title="Creative Commons BY 3.0">CC 3.0 BY</a>
        </footer>
      </div>
    )
  }
}

export default App;